<template>
  <div class="container">
    <ul id="messages" class="messages-container">
      <transition-group name="list" enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
        <li v-for="(message, index) in messages" :key="index"
            v-bind:class="{ 'message-container': (message.from !== myName), 'own-message-container': (message.from === myName)}">
          <div class="message">
            <span><b>{{ message.from == myName ? 'Me' : message.from  }}</b>: {{ message.text }}</span>
          </div>
        </li>
      </transition-group>
    </ul>
    <div class="input-container">
      <form @submit.prevent="sendMessage" class="message-input">
        <input type="text" placeholder="Enter your message" v-model="message">
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Chat",
  data() {
    return {
      myName: this.$route.params.name,
      lastMessageFrom: undefined,
      message: "",
      messages: []
    };
  },

  methods: {
    sendMessage() {
      this.$socket.emit("new_message", {
        message: this.message,
        from: this.myName
      });
      this.message = "";
    }
  },

  sockets: {
    connect() {
      this.isConnected = true;
    },

    disconnect() {
      this.isConnected = false;
    },

    connected_user() {},

    new_message(data) {
      this.lastMessageFrom = data.from;
      this.messages.push(data);

      setTimeout(() => {
        var container = this.$el.querySelector("#messages");
        container.scrollTop = container.scrollHeight + 1000;
      }, 20);
    }
  }
};
</script>

