import Vue from 'vue';
import Router from 'vue-router';
import Login from './components/Login.vue'
import Chat from './components/Chat.vue'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'login',
            component: Login
        },
        {
            path: '/chat/:name',
            name: 'chat',
            component: Chat
        }
    ]
});